import { ErrorMessage as RHFErrorMessage } from '@hookform/error-message';
import { useFormState } from 'react-hook-form';

import { FormErrorMessage } from '@wp-plugins/adapters';

export const ErrorMessage: React.FC<{ name: string }> = ({ name }) => {
	const { errors } = useFormState();

	return <RHFErrorMessage errors={errors} name={name} render={RenderErrorMessage} />;
};

export const RenderErrorMessage: React.ComponentProps<typeof RHFErrorMessage>['render'] = ({ message }) => {
	return <FormErrorMessage>{message}</FormErrorMessage>;
};
